<template>
  <div class="canvas">
    <div class="screenshot">
      <a-button type="primary" @click="screenshot">截图</a-button>
    </div>

    <canvas id="canvas"></canvas>
    <canvas id="canvas1"></canvas>
  </div>
</template>
<script setup>
import { onMounted, ref } from "vue";
let canvas = ref();
let ctx = ref();
onMounted(() => {
  canvas = document.getElementById("canvas1");
  ctx = canvas.getContext("2d");

  canvas.width = window.innerWidth;
  canvas.height = window.innerHeight;
  drawCanvas();
});
const drawCanvas = () => {
  const canvas = document.getElementById("canvas");
  const ctx = canvas.getContext("2d");
  canvas.width = 400;
  canvas.height = 400;
  ctx.beginPath();
  ctx.arc(200, 200, 200, 0, 2 * Math.PI);
  ctx.strokeStyle = "green";
  ctx.moveTo(200, 400);
  drawLine(ctx, 1);
  // ctx.lineTo(300, 300); // 连接到 x2, y2

  // ctx.lineTo(200, 200);
  // ctx.moveTo(300, 300);
  // ctx.lineTo(350, 200);
  // ...                                          // 更多的点连接
  //   ctx.closePath(); // 关闭路径
  //   ctx.fill();
  ctx.stroke();
};
const drawLine = (ctx, num) => {
  for (let index = 0; index < num; index++) {
    let x = parseInt((Math.random() * 10) / 2) + 1;
    let z = [];
    for (let index = 0; index < x; index++) {
      if (z) {
        ctx.moveTo(z, z);
      }
      let y = Math.random() * 400;
      console.log(y);
      ctx.lineTo(400 - y, 400 - y);
      z = y;
    }
  }
};
const screenshot = () => {
  ctx.clearRect(0, 0, canvas.width, canvas.height);
  console.log(document, document.documentElement);
  ctx.drawImage(document.documentElement, 0, 0);
  const imgData = canvas.toDataURL();
  console.log(imgData);
};
</script>
<style lang="scss" scoped>
.canvas {
  width: 100%;
  height: 100%;
  padding: 50px 0;
  .screenshot {
    position: absolute;
    top: 10px;
    left: 10px;
  }
  //   #canvas {
  //     width: 500px;
  //     height: 500px;
  //   }
}
</style>